@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: lato, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background-color: #dfdfdf;
  color: #333;
}
body::before {
  content:"";
  background-image: url("https://img.freepik.com/free-photo/healthy-diet-background-with-thyme-leaf-border_53876-106117.jpg?w=2000&t=st=1652955644~exp=1652956244~hmac=02aea698f193109c4e756ab22adbd9c3fbb60529934a1d0a11bde7c43dbca249");
  background-size: cover;
  height: 100vh;
  opacity: 0.4;
  position:absolute;
  top:0;
  bottom:0;
  right:0;
  left:0;
  z-index:-3;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.navbar {
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    padding: 20px;
    color: #333;
  }
  .navbar nav {
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
  }
  .navbar a.brand {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-right: auto;
    color: #333;
    text-decoration: none;
  }
  .navbar h1 {
    font-weight: 500;
  }
  .navbar img {
    margin-right: 5px;
    -webkit-filter: invert(20%);
            filter: invert(20%);
  }
  .navbar a:last-child {
    color: #333;
    text-decoration: none;
    margin-left: 40px;
    padding: 8px;
    border: 1px solid #333;
    border-radius: 4px;
  }
  .navbar a:last-child:hover {
    background: #333;
    color: #fff;
  }
.create {
  background-color: #fff;
  color: #333;
  padding: 10px 50px;
  max-width: 480px;
  margin: 40px auto;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
}
.create label span {
  display: block;
  margin: 30px 0 10px;
}
.create p {
  margin-top: 10px;
  font-size: 0.8em;
}
.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #333;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}
.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}

.home {
    max-width: 1200px;
    margin: 60px auto;
}
.recipe-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}
.recipe-list .card {
  background: #fff;
  color: #333;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}
.recipe-list .card:hover {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}
.recipe-list .card h3 {
  color: #333;
  margin-bottom: 6px;
}
.recipe-list .card p {
  color: #999;
  font-size: 0.9em;
}
.recipe-list .card div {
  color: #555;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}
.recipe-list .card a {
  color: #555;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}
.delete {
  height: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  -webkit-filter: invert(60%);
          filter: invert(60%);
}

.modal {
  position: fixed;
  color: #333;
  background-color: #fff;
  max-width: 480px;
  height: auto;
  top: 50%;
  left: 50%;
  border-radius: 4px;
  padding: 50px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1000;
}
.modal-header {
  
  height: -webkit-fit-content;
  
  height: -moz-fit-content;
  
  height: fit-content;
  width: 100%;
}
.modal label span {
  display: block;
  margin: 30px 0 10px;
  text-align: start;
}
.modal-title {
  color: #333 !important;
}
.modal p {
  margin-top: 10px;
  font-size: 0.8em;
}
.modal button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #333;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}

.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}
.close {
  height: 30px;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  -webkit-filter: invert(60%);
          filter: invert(60%);
}

.recipe {
  max-width: 1200px;
  margin: 40px auto;
  text-align: center;
  background: #fff;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  padding: 40px;
  box-sizing: border-box;
  position: relative;
}
.recipe ul {
  display: flex;
  padding: 0;
  justify-content: center;
  margin-top: 0;
}
.recipe li {
  list-style-type: none;
  margin-right: 10px;
  color: #777;
}
.recipe li::after {
  content: ",";
}
.recipe li:last-child::after {
  content: ".";
}
.method {
  text-align: left;
  line-height: 1.5em;
}
.update {
  height: 30px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  -webkit-filter: invert(60%);
          filter: invert(60%);
}
.App {
  min-height: 100%;
}
